// pages/favorites.tsx
import React, { useEffect, useState } from 'react';
import Link from 'next/link';

import Cookies from 'js-cookie';
import { GuitarCard } from '#/entities/guitar';

interface FavoriteItem {
  id: string;
  imageUrl: string;
  category: string;
  title: string;
  price: number;
}

export function FavoritesPage() {
  const [favorites, setFavorites] = useState<FavoriteItem[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Получаем ID избранных товаров из кук
    const favoritesIds = Cookies.get('favorites') 
      ? JSON.parse(Cookies.get('favorites') as string) 
      : [];

    // Здесь должен быть запрос к API для получения данных по ID
    // Для примера используем моковые данные
    const fetchFavorites = async () => {
      try {
        // В реальном приложении замените на запрос к вашему API
        // const response = await api.getItemsByIds(favoritesIds);
        // setFavorites(response.data);
        
        // Моковые данные для демонстрации
        const mockFavorites: FavoriteItem[] = favoritesIds.map((id: string) => ({
          id,
          imageUrl: `/images/products/${id}.jpg`,
          category: 'Гитары',
          title: `Гитара ${id}`,
          price: 10000 + Math.floor(Math.random() * 50000)
        }));

        setFavorites(mockFavorites);
      } catch (error) {
        console.error('Ошибка при загрузке избранного:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchFavorites();
  }, []);

  const removeFromFavorites = (id: string) => {
    const updatedFavorites = favorites.filter(item => item.id !== id);
    setFavorites(updatedFavorites);
    
    // Обновляем куки
    const favoritesIds = updatedFavorites.map(item => item.id);
    Cookies.set('favorites', JSON.stringify(favoritesIds), { expires: 365 });
  };

  if (loading) {
    return <div className="container">Загрузка...</div>;
  }

  return (
    <div className="container">
      <h1>Избранные товары</h1>
      
      {favorites.length === 0 ? (
        <div className="empty-favorites">
          <p>В избранном пока нет товаров</p>
          <Link href="/catalog">
            <button className="btn-primary">Перейти в каталог</button>
          </Link>
        </div>
      ) : (
        <div className="favorites-grid">
          {favorites.map(item => (
            <div key={item.id} className="favorite-item">
              <GuitarCard 
                {...item}
                onFavoriteToggle={() => removeFromFavorites(item.id)}
              />
              <button 
                onClick={() => removeFromFavorites(item.id)}
                className="remove-favorite"
              >
                Удалить из избранного
              </button>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}